* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

img {
  max-width: 100%;
  height: 250px;
  object-fit: cover;
  cursor: zoom-in;
}

.search-form {
  text-align: center;
  background-color: rgba(65, 77, 241, .753);
  margin-bottom: 20px;
  padding: 10px;
}

.input-value {
  border: none;
  border-radius: 8px;
  outline: none;
  padding: 10px;
}

.button-submit {
  cursor: pointer;
  background-color: #ff0;
  border: none;
  border-radius: 8px;
  padding: 6px;
  transition: transform .25s linear;
}

.button-submit:hover {
  transform: rotate(360deg);
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
  margin: auto;
  list-style: none;
  display: flex;
}

.info {
  gap: 10px;
  margin-left: 10px;
  margin-right: 10px;
  display: flex;
}

.photo-card {
  background-color: #fff;
  border-radius: 4px;
  flex-basis: calc(33.3333% - 100px);
  transition: transform .25s linear;
  box-shadow: 0 2px 1px -1px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12);
}

.photo-card:hover {
  animation: animate125 3s 1s infinite;
  transform: scale(1.1);
}

.load-more {
  visibility: hidden;
}

.style-btn {
  color: #fff8dc;
  cursor: pointer;
  background-color: #000;
  border-radius: 8px;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  transition: transform .25s linear;
  display: flex;
}

.style-btn:hover {
  animation: animate125 3s 1s infinite;
  transform: scale(1.4);
  box-shadow: 0 2px 1px -1px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12);
}

